/// <reference path='./xml.d.ts' />

import * as XmlGenerator from 'xml';
import { merge } from 'lodash';

import createRelationshipsXml, {IRelationship} from './createRelationshipsXml';
import createFeatureXml from './createFeatureXml';
import createIcon from './createIcon';
import constants from '../config/constants';
import serializationOptions from './serializationOptions';
import ISolution from '../models/packageDefinition/ISolution';
import IFeature from '../models/packageDefinition/IFeature';
import IXml from '../models/packageXml/IXml';
import IOpenDocumentConventionXml from '../models/packageXml/IOpenDocumentConventionXml';
import IPackageXml from '../models/packageXml/IPackageXml';
import IFeatureXml from '../models/packageXml/IFeatureXml';
import IIcon from '../models/packageXml/IIcon';
import IResources from '../models/packageXml/IResources';

export default
  function createSolutionXml(
    solution: ISolution, customFeatureFilenames: string[], resources: IResources): IPackageXml {
  solution = merge({
    version: '1.0.0.0'
  }, solution) as ISolution;

  // Create the top-level .rels file which points at the AppManifest
  const relationshipsXml: IXml = createRelationshipsXml('',
    [
      {
        type: 'package-manifest',
        target: constants.appManifestFileName
      }
    ]);

  // Create the top-level content types, defining content types in this package
  const contentTypesXml: IXml = {
    filename: constants.contentTypesXmlFilename,
    xml: XmlGenerator({
      Types: [
        {
          _attr: {
            xmlns: 'http://schemas.openxmlformats.org/package/2006/content-types'
          }
        },
        {
          Default: [
            {
              _attr: {
                Extension: 'xml',
                ContentType: 'text/xml'
              }
            }
          ]
        },
        {
          Default: [
            {
              _attr: {
                Extension: 'rels',
                ContentType: 'application/vnd.openxmlformats-package.relationships+xml'
              }
            }
          ]
        },
        {
          Default: [
            {
              _attr: {
                Extension: 'webpart',
                ContentType: 'text/xml'
              }
            }
          ]
        },
        {
          Default: [
            {
              _attr: {
                Extension: 'htm',
                ContentType: 'text/xml'
              }
            }
          ]
        },
        {
          Default: [
            {
              _attr: {
                Extension: 'aspx',
                ContentType: 'text/xml'
              }
            }
          ]
        },
        {
          Default: [
            {
              _attr: {
                Extension: 'resx',
                ContentType: 'text/xml'
              }
            }
          ]
        },
        {
          Default: [
            {
              _attr: {
                Extension: 'png',
                ContentType: 'application/wsp'
              }
            }
          ]
        },
        {
          Default: [
            {
              _attr: {
                Extension: 'jpg',
                ContentType: 'application/wsp'
              }
            }
          ]
        },
        {
          Default: [
            {
              _attr: {
                Extension: 'gif',
                ContentType: 'application/wsp'
              }
            }
          ]
        },
        {
          Default: [
            {
              _attr: {
                Extension: 'bmp',
                ContentType: 'application/wsp'
              }
            }
          ]
        }
      ]
    }, serializationOptions)
  };

  /* tslint:disable:no-any */
  const properties: any[] = [];
  /* tslint:disable:no-any */

  // Add title to properties
  properties.push({
    Title: solution.title ? solution.title : solution.name
  });

  // Add supported locales to properties, if needed
  /* tslint:disable:typedef */
  const supportedLocales = solution.supportedLocales && solution.supportedLocales.length > 0
  /* tslint:enable:typedef */
    ? solution.supportedLocales.map((locale: string) => {
      return {
        SupportedLocale: [
          {
            _attr: {
                CultureName: locale
            }
          }
        ]
      };
    })
    : undefined;

  if (supportedLocales) {
    properties.push({
      SupportedLocales: supportedLocales
    });
  }

  // Create the top-level AppManifest's XML, note that it is missing relationships
  const manifestXml: IOpenDocumentConventionXml = {
    filename: constants.appManifestFileName,
    /* tslint:disable:no-null-keyword */
    relationships: null,
    /* tslint:enable:no-null-keyword */
    xml: XmlGenerator({
      App: [
        {
          _attr: {
            xmlns: 'http://schemas.microsoft.com/sharepoint/2012/app/manifest',
            Name: solution.name,
            ProductID: solution.id,
            Version: solution.version,
            SharePointMinVersion: '16.0.0.0',
            IsClientSideSolution: 'true'
          }
        },
        {
          Properties: properties
        }
      ]
    }, serializationOptions)
  };

  // Collect the XML for the individual features
  const featuresXml: IFeatureXml[] =
    solution.features.map((feature: IFeature) => createFeatureXml(feature, resources));

  // Create icon data if a path to an icon is provided
  const icon: IIcon = solution.iconPath ? createIcon(solution.iconPath) : undefined;

  // Ensure the Manifest's relationships to features are stored
  let relsList: IRelationship[] = featuresXml.map((feature: IFeatureXml) => {
    return {
      type: 'manifest-feature',
      target: feature.filename
    };
  });
  relsList = relsList.concat(customFeatureFilenames.map((filename: string) => {
    return {
      type: 'manifest-feature',
      target: filename
    };
  }));

  relsList = relsList.concat(resources.localizedResourcePaths.map((filename: string) => {
    return {
      type: 'content-resource',
      target: filename
    };
  }));

  if (resources.defaultResourcePath) {
    relsList = relsList.concat({
        type: 'content-defaultresource',
        target: resources.defaultResourcePath
    });
  }

  if (icon) {
    relsList = relsList.concat({
        type: 'manifest-icon',
        target: icon.path
    });
  }

  manifestXml.relationships = createRelationshipsXml(constants.appManifestFileName, relsList);

  return {
    manifest: manifestXml,
    features: featuresXml,
    relationships: relationshipsXml,
    contentTypesXml: contentTypesXml,
    icon: icon,
    customFiles: undefined
  };
}